.Home {
    margin-left: 10%; 
    margin-top:0.5%; 
    margin-right: 10%;
    /* display:flex; */
    /* display: center; */
    margin-bottom: 5%;
    max-width: 90%;
    /* pointer-events: None; */
    text-decoration: none !important;
    color: rgba(44, 102, 144, 0.824);
}

.Home-div {
    /* text-indent: 1%; */
    text-decoration: none;
    line-height: 1.5;
    /* max-width: 80%; */
    /* pointer-events: None; */
}

.Home-sl{
    display: flex;
    flex-direction: column;
    text-align: left;
    text-decoration: none !important;
    font-size: 1em;
    color: #555;
    margin-top: 0.6em;
    /* margin-bottom: 0.6em; */
}

.Home-holder{
    background-color: rgb(197, 197, 197);
    width:40%;
}
/* .Home-img {
    max-width: 80%;
} */

.Home-rec{
    display: flex;
    margin-left: auto;
    max-width: 40%;
    /* margin-right: 3%; */
    margin-top: 5%;
    margin-bottom: 3%;
    /* pointer-events: none; */
}

.Home-Photo {
    display: flex;
    /* margin-left: auto; */
    /* max-width: 80%; */
    /* margin-right: 3%; */
    width: 100%;
    margin-top: 3%;
    margin-bottom: 3%;
    /* pointer-events: none; */
}

.Home-pic {
    max-width: 40%;
    /* display: flex; */
    /* flex-direction:column; */
    text-align: center;
    text-decoration: none;
    /* align-items: center; */
    /* margin-right:3%; */
    margin-left: 3%;
}
.Home-pic img{
    max-width: 90%;
    /* height: 50%; */
}

.Home Link{
    text-decoration: none;
}

.Home-con{
    display: flex;
    flex-direction: row;
    margin-left: 10%;
    margin-bottom: 5% ;
    align-items:flex-start;
    margin-top: 1em;
    max-width:100%;
    /* margin-left: 15%;
    margin-right: 20%;
    margin-bottom: 5%; */
}

.Home-s {
    display: flex;
    flex-direction: column;
    text-align: left;
    text-decoration: none !important;
    /* centers the text below the image */
    font-size: 0.7em;
    /* font size of the description */
    /* margin-top: 0.6em; */
    /* adds a little space between the image and the description */
    color: #555;
    /* sets the description text color to a dark gray */
}

.Home-sp {
    display: flex;
    text-align: left;
    /* max-width: 18%; */
    width: 13%;
    /* height:25%; */
    /* max-height: 1%; */
    margin-right: 3%;
}

.Home-d {
    text-align: center;
    text-decoration: none !important;
    /* centers the text below the image */
    font-size: 0.6em ;
    /* font size of the description */
    margin-top: 0.6em;
    /* max-width: 40%; */
    /* adds a little space between the image and the description */
    color: #555;
    /* sets the description text color to a dark gray */
}

.Home-t {
    text-align: left;
    text-decoration: none !important;
    /* centers the text below the image */
    font-size: 1em;
    /* font size of the description */
    margin-top: 0.8em;
    /* adds a little space between the image and the description */
    color: #555;
    font-weight: 700;
    /* sets the description text color to a dark gray */
}

.Home-iframe {
    margin-top: 10%;
}

.Home-icon{
    max-width: 40%;
}

@media screen and (max-width: 600px) {
    .Home {
        max-width: 90%;
        font-size: small;
        margin-left: 5%;
    }

    .Home-pic img{
        display: flexbox;
        /* max-width: 80%; */
    }

    .Home iframe{
        margin-top: 3%;
        width: 100% !important;
        height:200;
    }
}